.users{
    padding: 10px 10px 0 10px;
}
.users button{
    width: 100px;
    height: 25px;
    background-color: #8b005b;
    color: #0004ff;
    border-radius: 10px;
}

.container{
    display: flex;
    margin-bottom: 10px;
    border: #611e44 2px solid;
    border-radius: 10px;
}

.userPhoto{
    width: 100px;
    height: 100px;
    border-radius: 10px;
}

.fullNameAndStatus{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 30px;
    padding-right: 400px;
}
.location{
    display: flex;
    flex-direction: column;
    align-items: center;

}

.usersInfo{
    display: flex;
    width: 100%;
    justify-content: space-between;
}

.usersAvatar{
    padding: 5px;
    display: flex;
    flex-direction: column;
    align-items: center;
}
